.logo-title{
    font-size: 1.125rem;
}
.search-input{
    border-radius: 10rem;
    padding-left: 1rem;
    caret-color: $primary;
    font-weight: 400;
    color: $gray-800;
    &::placeholder{
        color: $gray-400;
        font-size: 0.875rem;
    }
}

.icon-rotate-hover{
    transform: rotate(0deg);
    transition: all .3s;
    
    &:hover{
        transform: rotate(180deg);
    }
}

.rotate-0{
    transform: rotate(0deg);
    transition: all .3s;
}

.rotate-180{
    transform: rotate(180deg);
    transition: all .3s;
}
.h-1{
    height: 600px;
}

.back-top{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: #fff;
    background: rgba(15,15,15,.15);
    font-size: 24px;

    position: fixed;
    bottom: 20px;//-50
    right: 20px;
    z-index: 10;
    transition: bottom .4s cubic-bezier(.35,.49,.53,.88);

    &:hover{
        background: #fff;
        color: $gray-500;
        
    }
}
